.navigation {
    height: 100px;
    display: flex;
    justify-content: space-between;
    margin-bottom: 25px;
    border-radius: 10px;
}
.logo-container {
    height: 80px;
    width: 80px;
}
  
.logo {
    margin-top: 25px;
    height: 50px;
    width: auto;
}
  
.nav-links-container {
    width: 50%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: flex-end;
}
  
.nav-link {
    color: black;
    text-decoration: none;
    padding: 10px 15px;
    cursor: pointer;
}
  
.sign-in {
    border: 1px solid #e9e9e9;
    border-radius: 5px;
}